@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,500;0,700;1,300&display=swap");

.bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.accounts {
  display: flex;
  gap: 10px;
  align-items: center;
  width: 100vw;
  margin: 0 0 15px 0;
  padding-top: 15px;
  justify-content: center;

  p {
    font-family: Roboto;
    font-size: 20px;
    font-style: italic;
    font-weight: 400;
    line-height: 31px;
    text-align: right;
    color: black;
    margin: 0;
    padding: 0;
  }

  img {
    width: 38px;
    height: 40px;
  }

  &-desktop {
    justify-content: flex-end;
    margin: 0 35px 5px 0;
    gap: 16px;
    p {
      font-size: 31px;
    }

    img {
      width: 64px;
      height: 68px;
    }
  }
}




